<template>
  <div
    class="richTxtBox boxBorder"
    :class="active"
    @mouseover="mouseOver"
    @mouseleave="mouseLeave"
    :style="{background:itemData.backgroundColor}"
  >
    <!-- 富文本组件 -->

    <div class="richTxt-box-info rt-fs16" style="width:100%;" v-show="itemData.html==''">
      <p class="rt-1 rt-mb20">点击编辑『富文本』内容 ==&gt;</p>
      <p class="rt-2 rt-mb20">
        可以对文字进行
        <strong class="rt-2 rt-mb20">加粗</strong>、
        <em class="rt-2 rt-mb20">斜体</em>、
        <span class="rt-2 rt-mb20" style="text-decoration: underline;">下划线</span>、
        <span class="rt-2 rt-mb20" style="text-decoration: line-through;">删除线</span>、文字
        <span class="rt-2 rt-mb20" style="color: rgb(0, 176, 240);">颜色</span>、
        <span
          class="rt-2 rt-mb20"
          style="background-color: rgb(255, 192, 0); color: rgb(255, 255, 255);"
        >背景色</span>、以及字号
        <span style="font-size: 20px;">大</span>
        <span style="font-size: 14px;">小</span>等简单排版操作。
      </p>
      <p class="rt-fs16 rt-mb20">还可以在这里加入表格了</p>
      <table class="rt-mb20 rt-fs16">
        <tbody>
          <tr>
            <td width="93" valign="top" style="word-break: break-all;">中奖客户</td>
            <td width="93" valign="top" style="word-break: break-all;">发放奖品</td>
            <td width="93" valign="top" style="word-break: break-all;">备注</td>
          </tr>
          <tr>
            <td width="93" valign="top" style="word-break: break-all;">兔兔</td>
            <td width="93" valign="top" style="word-break: break-all;">内测码</td>
            <td width="93" valign="top" style="word-break: break-all;">
              <em>
                <span style="color: rgb(255, 0, 0);">已经发放</span>
              </em>
            </td>
          </tr>
          <tr>
            <td width="93" valign="top" style="word-break: break-all;">小米</td>
            <td width="93" valign="top" style="word-break: break-all;">积分</td>
            <td width="93" valign="top" style="word-break: break-all;">
              <a href="javascript: void(0);" target="_blank">领取地址</a>
            </td>
          </tr>
        </tbody>
      </table>
      <p class="rt-1 rt-fs16" style="text-align: left;">
        <span class="rt-fs16" style="text-align: left;">也可在这里插入图片、并对图片加上超级链接，方便用户点击。</span>
      </p>
    </div>
    <div>
      <div
        v-html="itemData.html"
        class="ueContent"
        :style="{background:itemData.backgroundColor,padding:itemData.isFullScreen==0?0:'10px'}"
      ></div>
    </div>
    <!-- 左侧介绍 -->
    <span class="box-title">{{itemData.name}}</span>
    <i class="delete-btn" v-if="deleteBtn" @click.stop="deleteBox(itemData)">
      <img src="@/assets/decorateBox/deleteBox.png" alt />
    </i>
  </div>
</template>

<script>
export default {
  props: {
    //父级传递的数据
    itemData: {
      type: Object
    }
  },
  data() {
    return {
      active: "", //绑定样式
      deleteBtn: false
    };
  },
  created() {
    console.log(this.itemData.html);
  },
  methods: {
    // 移入事件
    mouseOver() {
      this.active = "active";
      this.deleteBtn = true;
    },
    // 移出事件
    mouseLeave() {
      this.active = "";
      this.deleteBtn = false;
    },
    //删除当前组件盒子事件
    deleteBox(box) {
      this.$emit("deleteBox", box);
    }
  }
};
</script>

<style scoped lang="less">
.delete-btn {
  position: absolute;
  width: 32px;
  height: 32px;
  cursor: pointer;
  z-index: 3;
  top: 10px;
  right: -32px;
}
.box-title {
  position: absolute;
  cursor: pointer;
  z-index: 3;
  top: 0px;
  left: -70px;
  height: 24px;
  font-size: 12px;
  background: rgba(255, 255, 255, 1);
  box-shadow: 2px 2px 4px 0px rgba(80, 80, 80, 0.2);
  color: rgba(102, 102, 102, 1);
  line-height: 24px;
  width: 64px;
  text-align: center;
  border-radius: 2px;
}
.box-title:after,
.box-title:before {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  border: 4px solid transparent;
  left: 50%;
  margin-left: 32px;
  bottom: 8px;
}

.box-title:after {
  border-left-color: #fff;
}
.richTxt-box-info {
  box-sizing: border-box;
  padding: 10px;
  .rt-mb20 {
    margin-bottom: 20px;
  }
  .rt-fs16 {
    font-size: 16px;
  }
  table {
    border-collapse: collapse;
    border-spacing: 0;
    width: 100%;
  }
  td,
  th {
    padding: 5px 10px;
    border: 1px solid #ddd;
    font-size: 16px;
  }
}
.ueContent /deep/ img {
  max-width: 100%; /*图片自适应宽度*/
}
</style>
